body{
    background-color: #ACADA8;
}
.layout{
    width: 98%;
    margin: auto;
}
.header__main{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.header__list{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.header__list ul{
    display: flex;
    flex-direction: row;
}
.header__list li{
    list-style: none;
    margin-left: 25px;
}
.header__list li a{
    color: white;
    text-decoration: none;
    font-size: 20px;
    cursor: pointer;
}
.active{
    border-bottom: 2px solid white;
}
.logo{
    width: 150px;
}
.logo img{
    width: 100%;
}
.footer{
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    color: white;
    border-top: 2px solid white;
    height: 50px;
    padding-top: 10px;
    align-items: center;
    text-align: center;
    font-size: 25px;
    background-color: #ACADA8;
}
.add__product__main{
    display: flex;
    flex-direction: column;
    width: 75%;
    margin: auto;
    margin-top: 100px;
    margin-bottom: 150px;
}
.product__setting{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.product__setting div{
    width: 100%;
}
.button__add{
    display: flex;
    margin-top: 150px;
    justify-content: right;
}
.input__name{
    width: 300px;
}
.input__description input{
    min-height: 150px;
}
.set__image{
    margin-top: 15px;
    display: flex;
    flex-direction: row;
    align-items: center;
}
.home__main{
    display: flex;
}
.home__add__product{
    display: flex;
    position: fixed;
    margin-top: 13%;
    flex-direction: column;
    left: 70%;
}
.search__product{
    display: flex;
    justify-content: center;
}
.list__product{
    margin-top: 50px auto;
    margin-bottom: 100px;
}
.product__main{
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-top: 40px;
    border: 3px solid wheat;
}
.mini__img{
    display: flex;
    width: 100%;
}
.mini__img img{
    display: flex;
    width: 100%;
}
.show__tovar{
    width: 40%;
    margin-left: 10%;
}
.mini__product__name__with__price{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 95%;
    margin: auto;
}
.product__main h1{
    text-align: center;
}
.price__with__button{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}
.mini__price{
    color: red;
    align-items: center;
    margin-top: 8px;
}
.button__buy{
    height: 65px;
}
.show__product__img{
    width: 80%;
}
.show__product__img img{
    width: 100%;
}
.price__name{
    align-items: center;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.price{
    color: red;
    font-size: 20px;
}
.message{
    color: greenyellow;
    font-size: 40px;
    margin: 100px auto;
}
.footer{
    margin-top: 90px;
}
.show__product__main{
    margin-bottom: 150px;
}